<template>
    <div class="buy-movie">
        <van-nav-bar
            title="座位选择"
            left-text="返回"
            left-arrow
            @click-left="back"
        />
        <div class="content clearfix">
            <div class="img fl">
                <img class="auto-img" :src="small" alt="">
            </div>
            <div class="title-box fl">
                <div class="title movie-name">{{data.title}}</div>
                <div class="title movie-type">{{data.genresInfo}}</div>
                <div class="title movie-time">{{movieTime}}</div>
                <div class="title time">{{data.pubdate}}</div>
                <div class="fr price-box"><span class="price">￥40</span><span>/张</span></div>
            </div>
        </div>

        <!-- 座位 -->
        <div class="chair-box">
            <div class="chair"  v-for="(item, index) in chairDatas" :key="index">
                <span v-for="(value, key) in item" :key="key" :class="{buying: value.type == 'buying', selected: value.type == 'selected'}"  @click="selectChair(value)"></span>
            </div>
        </div>

        <div class="tick-box">
            <ul class="tick clearfix" :style="{width: userChairData.length * 93 + 'px'}">
                <li ref="userchair" v-for="(item, index) in userChairData" :key="index"><span class="c c1"></span><i class="r r1"></i>{{item.row}}排{{item.col}}座<span class="c c2"></span><i class="r r2"></i></li>
            </ul>
        </div>

        <!-- 座位标志 -->
        <div class="chair-flag clearfix">

            <div>
            <div class="c-box clearfix">
                <i class="c-bg fl c-bg1"></i><span class="fl buyed">可选</span>
            </div>
            </div>

            <div>
            <div class="c-box clearfix m-box">
                <i class="c-bg fl c-bg2"></i><span class="fl buyed">已选</span>
            </div>
            </div>

            <div>
            <div class="c-box clearfix fr">
                <i class="c-bg fl c-bg3"></i><span class="fl buyed">已售</span>
            </div>
            </div>

        </div>


        <div class="buy-tick">
            <span class="fl total">合计：￥ <span class="allPrice">{{allPrice}}.00</span> </span>
            <span class="fr pay" @click="subOrder(data,userChairData,allPrice)">确认支付</span>
        </div>



        <!-- <div>
            <router-view />
        </div> -->

    </div>
</template>

<script>
    export default {
        name:'buyMovie',
        data(){
            return {
                data:[],
                small:'',
                average:'',
                movieTime:'',

                //座位数据
                chairDatas: [
                    [
                        {row: 1, col: 1, type: ''}, 
                        {row: 1, col: 2, type: ''}, 
                        {row: 1, col: 3, type: ''},
                        {row: 1, col: 4, type: ''},
                        {row: 1, col: 5, type: ''},
                        {row: 1, col: 6, type: ''},
                        {row: 1, col: 7, type: ''},
                        {row: 1, col: 8, type: ''},
                        {row: 1, col: 9, type: ''},
                        {row: 1, col: 10, type: ''},
                    ],
                    [
                        {row: 2, col: 1, type: ''}, 
                        {row: 2, col: 2, type: ''}, 
                        {row: 2, col: 3, type: ''},
                        {row: 2, col: 4, type: ''},
                        {row: 2, col: 5, type: ''},
                        {row: 2, col: 6, type: ''},
                        {row: 2, col: 7, type: ''},
                        {row: 2, col: 8, type: ''},
                        {row: 2, col: 9, type: ''},
                        {row: 2, col: 10, type: ''},
                    ],
                    [
                        {row: 3, col: 1, type: ''}, 
                        {row: 3, col: 2, type: ''}, 
                        {row: 3, col: 3, type: ''},
                        {row: 3, col: 4, type: ''},
                        {row: 3, col: 5, type: ''},
                        {row: 3, col: 6, type: ''},
                        {row: 3, col: 7, type: ''},
                        {row: 3, col: 8, type: ''},
                        {row: 3, col: 9, type: ''},
                        {row: 3, col: 10, type: ''},
                    ],
                    [
                        {row: 4, col: 1, type: ''}, 
                        {row: 4, col: 2, type: ''}, 
                        {row: 4, col: 3, type: ''},
                        {row: 4, col: 4, type: ''},
                        {row: 4, col: 5, type: ''},
                        {row: 4, col: 6, type: ''},
                        {row: 4, col: 7, type: ''},
                        {row: 4, col: 8, type: ''},
                        {row: 4, col: 9, type: ''},
                        {row: 4, col: 10, type: ''},
                    ],
                    [
                        {row: 5, col: 1, type: ''}, 
                        {row: 5, col: 2, type: ''}, 
                        {row: 5, col: 3, type: ''},
                        {row: 5, col: 4, type: ''},
                        {row: 5, col: 5, type: ''},
                        {row: 5, col: 6, type: ''},
                        {row: 5, col: 7, type: ''},
                        {row: 5, col: 8, type: ''},
                        {row: 5, col: 9, type: ''},
                        {row: 5, col: 10, type: ''}
                    ],
                    [
                        {row: 6, col: 1, type: ''}, 
                        {row: 6, col: 2, type: ''}, 
                        {row: 6, col: 3, type: ''},
                        {row: 6, col: 4, type: ''},
                        {row: 6, col: 5, type: ''},
                        {row: 6, col: 6, type: ''},
                        {row: 6, col: 7, type: ''},
                        {row: 6, col: 8, type: ''},
                        {row: 6, col: 9, type: ''},
                        {row: 6, col: 10, type: ''}
                    ],
                    [
                        {row: 7, col: 1, type: ''}, 
                        {row: 7, col: 2, type: ''}, 
                        {row: 7, col: 3, type: ''},
                        {row: 7, col: 4, type: ''},
                        {row: 7, col: 5, type: ''},
                        {row: 7, col: 6, type: ''},
                        {row: 7, col: 7, type: ''},
                        {row: 7, col: 8, type: ''},
                        {row: 7, col: 9, type: ''},
                        {row: 7, col: 10, type: ''}
                    ]
                ],

                userChairData:[],

                allPrice:0.00

            }
        },

        created(){

            this.$toast({
                duration:0,
                message: '加载中......'
            })
            //截取路由参数
            let id = this.$route.params.id;
            // console.log('id ==>',id);


            //获取用户的信息数据
            let orderData = localStorage.getItem('orderData')
            //防止空用户
            orderData = orderData ? JSON.parse(orderData) : [];
            // console.log('orderData ==>',orderData)


            orderData.forEach( v => {        
                if(v.data.id === id){
                    v.userChairData.forEach( k => {
                        this.chairDatas.forEach( i => {
                            i.forEach( j => {
                                if(j.col === k.col && j.row === k.row){
                                    j.type = 'buying'
                                }
                            })
                        })
                    })                   
                }
            })


            
            // this.chairDatas.forEach( v => {
                
            //     v.forEach( i => {
            //         orderData.forEach( j => {                      
            //             j.userChairData.forEach( k => {
            //                 if(i.col == k.col && i.row == k.row){
            //                     i.type = 'buying'
            //                 }
            //             })                                          
            //         })
            //     })       
            // })

            this.axios({
                method: 'GET',
                url: 'https://douban.uieee.com/v2/movie/subject/' + id
            }).then(result => {
                // console.log('result ==>',result)
                //将电影类型合并
                result.data.genresInfo = result.data.genres.join(' / ');

                this.data = result.data
                // console.log('this.data ==>',this.data)
                this.small = this.data.images.small
                // console.log('this.small ==>',this.small)
                this.average = this.data.rating.average
                this.movieTime = this.data.durations[0]


                this.$toast.clear();
            }).catch(err => {
                // console.log('出错了')
                this.$toast.clear();
            })



        },

        methods:{
            back(){
                this.$router.go(-1)
            },

            selectChair(item){
                // console.log('item ==>',item)
                //不能选择已经购买的
                if (item.type == 'buying') {
                    return;
                }
                //没有选择的, 高亮显示座位
                if (item.type == '') {           
                    item.type = 'selected';
                    this.userChairData.push(item)
                    this.price();
                } else if(item.type = 'selected'){
                    item.type = '';
                    this.userChairData.forEach(v => {
                        if(v == item){
                            this.userChairData.splice(v,1);
                            this.price();
                        }
                    });
                }
            },

            price(){
                this.allPrice = this.userChairData.length * 40
            },

            subOrder(data,userChairData,i){
                //获取用户的信息数据
                let orderData = localStorage.getItem('orderData')
                //防止空用户
                orderData = orderData ? JSON.parse(orderData) : [];

                let _this = this;
                if(this.allPrice = 0){
                    return;
                }else {

                    let randomTime = Math.random() * 3 * 1000;

                    setTimeout(function (){
                        let time = new Date().getTime();
                        let o = {
                            data:data,
                            userChairData:userChairData,
                            allPrice:i,
                            // time
                        }
                        // console.log('o ==>',o)

                        orderData.unshift(o)
                        // 信息保存到本地
                        localStorage.setItem('orderData',JSON.stringify(orderData));
                        // console.log('orderData ==>',orderData)


                        _this.$toast({
                            duration: 2000,
                            message: '支付成功'
                        })

                        setTimeout(function (){     
                            _this.$router.push({name:'order'})
                        },3000)

                        

                    },randomTime)


                    
                    
                }

            }
        }
        
    }
</script>

<style lang="less" scoped>

.buy-movie{

    .content{
        overflow: hidden;
        // background-color:#ddd;
        padding: 10px;
        border-bottom: 1px solid #ddd;

        .img{
            width: 90px;
            height: 130px;
        }

        .title-box{
            margin-left: 10px;
            width:calc(~"100% - 100px");
            height:130px;
        }

        .title{
            font-size: 12px;
            max-width: 170px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            margin-bottom: 10px;
        }
        .movie-name{
            font-size: 18px;
            font-weight: bold;
            
        }
        .number{
            font-size: 16px;
            color: red;
            line-height: 16px;;
        }  

        .price-box{
            margin-right: 40px;
            margin-top: -20px;
        }

        .price{
            color: red;
            font-size: 18px;;
        }
    }

    .chair-box{
        padding: 0 27px;
        margin-top: 20px;

        .chair{
            margin: 0 auto;
            height: 24px;
            margin-bottom: 8px;


            >span{
                float: left;
                width: 24px;
                height: 24px;
                margin: 0 4px;
                background: url("../assets/image/chair1.png") no-repeat center center;
                background-size: 24px 24px;
                    &.selected{
                        background-image: url("../assets/image/chair2.png")
                    }
                    &.buying{
                        background-image: url("../assets/image/chair3.png")
                    }
            }
        }
    }

    .tick-box{
        overflow-x: auto;
        overflow-y: hidden;
        margin: 0 10px;
        margin-top: 30px;
        .tick{
            height: 36px;
            >li{
                float: left;
                width: 80px;
                height: 32px;
                border: 1px solid #FF9800;
                position: relative;
                text-align: center;
                font-size: 16px;
                line-height: 32px;
                background-color: #FFBB42;
                color: #fff;
                margin-right: 10px;
            }
            .r{
                position: absolute;
                width: 10px;
                height: 10px;
                border: 1px solid #FF9800;
                
                top: 0;
                bottom: 0;
                margin: auto 0;
                border-radius: 50%;
                background-color: #fff;
                z-index: 1;
            }
            .r1{
                left: -7px;
            }
            .c1{
                left: -8px;
            }
            .r2{
                right: -7px;
            }
            .c2{
                right: -8px;
            }
            .c{
                position: absolute;
                top: 0;
                bottom: 0;
                background-color: #fff;
                width: 7px;
                z-index: 2;
            }
        }
    }


    .chair-flag{
        margin: 20px 80px 0;
        .c-box{
            width: 50px;
        }
        .m-box{
            margin: 0 auto;
        }
        >div{
            float: left;
            width: calc(~"100% / 3");
            height: 30px;
            .c-bg{
            width: 12px;
            height: 8px;
            margin-top: 12px;
            &.c-bg1{
                background-color: #E7E6E5;
            }
            &.c-bg2{
                background-color: #1AFA29;
            }
            &.c-bg3{
                background-color: #D81E06;
            }
            }
            .buyed{
            line-height: 30px;
            font-size: 10px;
            margin-left: 5px;
            }
        }
    }

    .buy-tick{
      position: absolute;
      bottom: 0;
      left: 0;
      height: 54px;
      width: calc(~"100% - 20px");
      background-color: #fff;
      box-shadow: 0px 0px 5px 1px #ccc;
      padding: 0 10px;
      .total{
        line-height: 54px;
        font-size: 16px;
      }
      .pay{
        width: 80px;
        height: 34px;
        // background-image: linear-gradient(to bottom, #FED73E, #FFBE46);
        background-color: #D81E06;
        color: #fff;
        font-size: 13px;
        text-align: center;
        line-height: 34px;
        border-radius: 10px;
        border-bottom: 2px solid #FF9A00;
        letter-spacing: 2px;
        margin-top: 10px;
      }

      .allPrice{
          color: red;
      }
    }
    

}

</style>